:root {
  --clr-primary: #002555;
  --clr-secondary: #C0392B;
  --clr-tertiary: #F1C40F;
  --clr-dark-gray: #BABCC2;
  --clr-light-gray: #CDCDCD;
  --clr-background: #F2F2F2;
  --clr-button-hover: #E6E6E6;
  --clr-font: #131313;
  --clr-screen: #1A1A1A;
  --clr-active: #53FF5C;
  
  --clr-twitter: #1DA1F2;
  --clr-whatsapp: #25D366;
  --clr-github: #333333;
}

.container {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loader {
  background-color: var(--clr-dark-gray);
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  transition: opacity 0.75s, visibility 0.75s;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: "";
  height: 50px;
  width: 50px;
  border: 10px solid var(--clr-background);
  border-top-color: var(--clr-primary);
  border-radius: 50%;
  animation: loading 1s ease infinite;
}

@keyframes loading {
  from { transform: rotate(0turn);}
  to { transform: rotate(1turn);}
}

.background {
  background-color: var(--clr-background);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mesa-bg {
  margin-top: -15px;
}

.mesa {
  /*border: 1px solid red;*/
  margin-top: -450px;
  height: 400px;
  /*width: 800px;*/
  display: flex;
  flex-direction: row;
}

.left-side {
  width: 450px;
  display: flex;
  flex-direction: column;
}

.header {
  line-height: 54px;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.title {
  color: var(--clr-primary);
  font-size: 54px;
}

.version {
  color: var(--clr-secondary);
  font-size: 16px;
  font-style: italic;
  margin-left: 10px;
}

.actions {
  height: 300px;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.group {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  background-color: var(--clr-background);
  color: var(--clr-font);
  height: 45px;
  width: 45px;
  border-radius: 2px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  transition: all ease 0.2s;
}

.button:hover {
  background-color: var(--clr-button-hover);
  cursor: pointer;
}

.active {
  background-color: var(--clr-active);
  color: var(--clr-font);
  height: 45px;
  width: 45px;
  border-radius: 2px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  transition: all ease 0.2s;
}

.label {
  background-color: var(--clr-background);
  color: var(--clr-font);
  height: 20px;
  width: 60px;
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
}

.line {
  background: var(--clr-primary);
  height: 2px;
  margin-top: 15px;
}

.right-side {
  /*border: 1px solid red;*/
  width: 300px;
  margin-top: 10px;
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.screen {
  background-color: var(--clr-screen);
  color: var(--clr-tertiary);
  line-height: 30px;
  height: 90px;
  width: 240px;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
}

.song, .type {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.bgm {
  /*border: 1px solid red;*/
  width: 192px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.options {
  margin-top: 50px;
  height: 110px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.fake-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.stop, .play {
  background-color: var(--clr-background);
  color: var(--clr-font);
  height: 45px;
  width: 80px;
  border-radius: 2px;
  margin: 0 9px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
}

.rec {
  background-color: var(--clr-background);
  color: var(--clr-secondary);
  height: 45px;
  width: 45px;
  border-radius: 2px;
  margin: 0 9px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
}

.share-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.share-buttons a {
  color: var(--clr-font);
  text-decoration: none;
  transition: all ease 0.2s;
}

.twitter, .whatsapp, .github {
  background-color: var(--clr-background);
  color: var(--clr-font);
  height: 45px;
  width: 45px;
  border-radius: 2px;
  margin: 0 9px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  line-height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  transition: all ease 0.2s;
}

.twitter:hover {
  background-color: var(--clr-button-hover);
  color: var(--clr-twitter);
  cursor: pointer;
}

.whatsapp:hover {
  background-color: var(--clr-button-hover);
  color: var(--clr-whatsapp);
  cursor: pointer;
}

.github:hover {
  background-color: var(--clr-button-hover);
  color: var(--clr-github);
  cursor: pointer;
}

.footer {
  color: var(--clr-font);
  position: absolute;
  bottom: 10px;
}

.footer a {
  font-weight: 500;
  text-decoration: none;
}

/******************************************
  Media queries
******************************************/

/* Smartphones */
@media screen and (max-width: 400px) {
  .background {
    position: relative;
    left: 0px;
  }
  .mesa-bg {
    position: relative;
    left: 0px;
  }
  .mesa-bg img {
    width: 100vw;
  }
  .mesa {
    height: 200px;
    width: 380px;
    margin: -215px 20px 0 20px;
    z-index: 5;
  }
  .left-side {
    width: 60%;
  }
  .header {
    line-height: 24px;
  }
  .title {
    font-size: 24px;
  }
  .version {
    font-size: 10px;
    margin-left: 2px;
  }
  .actions {
    height: 130px;
    margin-top: 8px;
  }
  .row {
    margin-bottom: 4px;
  }
  .button {
    height: 25px;
    width: 25px;
    margin-bottom: 5px;
    font-size: 12px;
    line-height: 12px;
  }
  .label {
    height: 10px;
    width: 30px;
    margin: 0px 2px;
    font-size: 4.5px;
    margin-bottom: 5px;
  }
  .line {
    margin-top: 20px;
  }
  .right-side {
    width: 40%;
  }
  .screen {
    font-size: 6px;
    line-height: 6px;
    height: 40px;
    width: 100px;
    padding: 8px;
    border-radius: 8px;
    justify-content: space-around;
  }
  .song {
    width: 80px;
  }
  .options {
    margin-top: 20px;
    height: 20px;
  }
  .stop, .play {
    height: 25px;
    width: 40px;
    margin: 0 4px;
    font-size: 12px;
    line-height: 12px;
  }
  .rec {
    height: 25px;
    width: 25px;
    margin: 0 4px;
    font-size: 12px;
    line-height: 12px;
  }
  .twitter, .whatsapp, .github {
    height: 25px;
    width: 25px;
    margin: 5px 4px;
    font-size: 12px;
    line-height: 12px;
  }
  .footer {
    position: absolute;
    bottom: 10px;
  }

}
